<template>
  <div class="address_book">
    <div class="address_b_header">
      <CContentHeader content="流程控制" />
    </div>

    <div class="address_b_con flex">
      <div class="adbc_bmnav flex flex-column">
        <p class="adbc_bmnav_title">组织架构</p>
        <div class="adbc_bmnav_search flex">
          <div class="adbc_bmnav_searchwrap">
            <img class="adbc_bmnav_searchwrap_icon" :src="require('../../assets/img/addressBook/search.png')" alt />
            <el-input placeholder="输入关键字" v-model="input"></el-input>
          </div>
          <el-button type="primary" @click="addDepartment">添加部门</el-button>
        </div>

        <div class="adbc_bmcon flex-one">
          <!-- <DepTree @item="clickDep" @loadMore="depLoadMore" :corpData="corpList"/> -->
          <!-- 左侧导航树的id为 ad10 -->
          <DepTree corpTreeId="ad10" searchType="dep" :isUseSpot="true" />
        </div>
      </div>

      <div class="adbc_list">
        <p class="adbc_bmnav_title">{{clickShowDep.name}}</p>

        <div class="adbc_list_opeara flex">
          <div class="adbc_list_opeara_list">
            <!-- <el-button type="primary">添加成员</el-button> -->
            <el-button type="primary" @click="changeDepartment">调整部门</el-button>
            <el-button type="danger" @click="deleteUesr">批量删除</el-button>
          </div>
          <div class="adbc_list_opeara_right">
            <el-button type="primary" @click="addNewMember">邀请新成员</el-button>
          </div>
        </div>

        <div class="abdc_list_con">
          <template>
            <el-table :data="tableData" ref="multipleTable" @selection-change="handleSelectionChange" style="width: 100%" :header-cell-style="setTableNavColor">
              <!-- <el-table-column max-width="50">
                <div class="adbc_list_con_itemselect">
                  <img src="https://ss2.bdstatic.com/70cFvnSh_Q1YnxGkpoWK1HF6hhy/it/u=2210972854,1802121898&fm=27&gp=0.jpg" alt="">
                </div>
              </el-table-column>-->
              <el-table-column type="selection" width="40"></el-table-column>
              <el-table-column prop="name" label="姓名"></el-table-column>
              <el-table-column prop="position" label="职位"></el-table-column>
              <el-table-column prop="id" label="工号"></el-table-column>
              <el-table-column prop="phone" label="手机" min-width="100"></el-table-column>
              <el-table-column prop="email" label="邮箱" min-width="150"></el-table-column>
              <el-table-column label="操作">
                <template slot-scope="scope">
                  <div class="abdc_list_con_opearte flex">
                    <p>{{(scope.row.isEnable === 0)?'未启用':'已启用' }}</p>
                    <!-- abdc_list_con_opearte_wrap_active abdc_list_con_opearte_wrap_nactive  -->
                    <div class="abdc_list_con_opearte_wrap" :class="[(scope.row.isEnable === 0)?'abdc_list_con_opearte_wrap_nactive':'abdc_list_con_opearte_wrap_active']" @click="handleEnable(scope.row)">
                      <img class="abdc_list_con_opearte_icon" :src="require('../../assets/img/addressBook/slide.png')" />
                    </div>
                  </div>
                </template>
              </el-table-column>
              <el-table-column label="激活状态">
                <template slot-scope="scope">
                  <span v-if="scope.row.isActive=== 0" @click="invitationActive(scope.row)" class="ab_invitation_active">邀请激活</span>
                  <span v-if="scope.row.isActive=== 1">已激活</span>
                  <!-- <span>{{getAAA(scope.row)}}</span> -->
                </template>
              </el-table-column>
            </el-table>
          </template>
        </div>
      </div>
    </div>
    <!-- 选择部门的遮罩层 -->
    <div class="select_dep">
      <MaskLayer title="设置所在部门" corpTreeId="ad12" :dialogShow.sync="dialogShow" searchType="dep" twoTitle="成员将属于以下部门" :powerList="powerList" @submit="submitBtn" @reset="resetBtn"></MaskLayer>
    </div>

    <!-- 遮罩层 -->
    <div class="ab_add_department" v-if="isShowCover" @click="clickCoverBlank">
      <!-- 添加部门 -->
      <div class="ab_add_department_wrap" v-if="isShowAddDepartments" @click="clodeAddCoverStop">
        <div class="ab_add_department_wrap_h clearfix">
          <p class="ab_add_department_wrap_h_title fl">添加部门</p>
          <div @click="clodeAddCover" class="ab_add_department_wrap_h_close_wrap fr flex flex-item">
            <img class="ab_add_department_wrap_h_close" :src="require('../../assets/img/addressBook/close.png')" />
          </div>
        </div>

        <div class="ad_add_d_inp ad_add_d_inp1 flex flex-item">
          <p>部门名称：</p>
          <el-input placeholder="请输入名称" v-model="departInput"></el-input>
        </div>

        <!-- ad_add_d_inp2_focus 获取焦点的时候加上这个 class 类名就变灰色 -->
        <div class="ad_add_d_inp ad_add_d_inp2 flex flex-item">
          <p>上级部门：</p>
          <el-input placeholder="请选择" :class="{'active': isFocusSelectDepartInput}" @focus="selectDepartInputFocus" v-model="selectDepartInput.val"></el-input>
          <img class="ad_add_d_inp2_icon" :src="require('../../assets/img/addressBook/open2.png')" />
          <div class="ad_add_d_inp2_list" v-if="isFocusSelectDepartInput">
            <DepTree corpTreeId="ad11" :searchType="'dep'" />
          </div>
        </div>
        <div class="ad_add_btn_wrap">
          <p class="ad_add_btn" @click="addDepBtn">新建部门</p>
        </div>
      </div>

      <!-- 邀请新成员 -->
      <div class="ab_add_add_newmemeter" v-if="isShowAddNewMember" @click="clodeAddCoverStop">
        <div class="ab_add_department_wrap_h clearfix">
          <p class="ab_add_department_wrap_h_title fl">邀请新成员</p>
          <div @click="clodeAddCover" class="ab_add_department_wrap_h_close_wrap fr flex flex-item">
            <img class="ab_add_department_wrap_h_close" :src="require('../../assets/img/addressBook/close.png')" />
          </div>
        </div>
        <div class="ad_add_newmember_con">
          <p class="ad_add_newmember_con_title">分享二维码通知加入</p>
          <p class="ad_add_newmember_con_tips">成员扫码后快速加入企业</p>
        </div>
        <div class="ad_add_newmember_con_qrcode">
          <img :src="require('../../assets/img/addressBook/qrcode.png')" alt />
        </div>
        <div class="ad_add_newmember_con_copyurl">复制邀请链接</div>
      </div>

      <!-- 添加子部门 -->
      <div class="ad_add_child_dep">
        <div class="ab_add_department_wrap" v-if="isShowAddDep" @click="clodeAddCoverStop">
          <div class="ab_add_department_wrap_h clearfix">
            <p class="ab_add_department_wrap_h_title fl">{{isAddOrChange == 'add' ? '添加部门':'修改部门名称'}}</p>
            <div @click="clodeAddCover" class="ab_add_department_wrap_h_close_wrap fr flex flex-item">
              <img class="ab_add_department_wrap_h_close" :src="require('../../assets/img/addressBook/close.png')" />
            </div>
          </div>

          <div class="ad_add_d_inp ad_add_d_inp1 flex flex-item">
            <p>部门名称：</p>
            <el-input placeholder="请输入名称" autofocus="true" v-model="addChildrendepInput"></el-input>
          </div>

          <div class="ad_add_btn_wrap">
            <p class="ad_add_btn" @click="addChildrenDepBtn">{{isAddOrChange == 'add' ? '新建部门':'保存部门名称'}}</p>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import DepTree from "../../components/c_departmentTree.vue";
import MaskLayer from "../../components/c_maskLayer";
export default {
  data() {
    return {
      input: "",
      isShowCover: false, // 是否显示遮罩层
      isShowAddDepartments: false, // 是否显示添加成员
      isShowAddNewMember: false, // 是否显示添加新成员
      isShowDelMember: false, // 是否显示批量删除按钮
      isShowAddDep: false, // 是否显示新增部门

      departInput: "", // 弹出遮罩层部门名称
      selectDepartInput: {
        // 选择下拉部门列表中的值
        val: "",
        depid: ""
      }, // 弹出遮罩层上级部门

      // 是新建部门还是修改部门名称 新建和修改部门名称用的同一个遮罩层
      isAddOrChange: "", // 新建 ’add‘ 修改部门名称 'change'
      addChildrendepParentId: "", //添加子部门的父级部门id
      addChildrendepInput: "", // 添加子部门的部门名称
      changeDepNameId: "", // 修改当前部门的id

      clickShowDep: {}, // 当前点击的左侧的部门列表

      isFocusSelectDepartInput: false,

      isFocusSelect: false, // 弹出遮罩层上级部门的input框是否获得焦点

      multipleSelection: [], // element 选择的容器
      tableData: [
        // {
        //   id: "11",
        //   name: "王小虎",
        //   position: "root",
        //   phone: "1591992104",
        //   email: "60178917@qq.com",
        //   isEnable: 0,
        //   isActive: 1
        // },
        // {
        //   id: "11",
        //   name: "王小虎",
        //   position: "root",
        //   phone: "1591992104",
        //   email: "60178917@qq.com",
        //   isEnable: 0,
        //   isActive: 1
        // }
      ],

      // 修改成员所在部门的
      dialogShow: false,
      powerList: []
    };
  },

  methods: {
    // 修改部门所在成员 start

    // 点击调整部门按钮
    changeDepartment() {
      console.log("调整部门");
      console.log(this.multipleSelection);
      if (this.multipleSelection.length === 0) {
        return this.$zero.ToastError("请选择要调整的人员");
      }
      // 打开弹出层
      this.dialogShow = true;
    },

    // 调整成员所在部门
    submitBtn() {
      console.log("点击了确定");
      console.log(this.powerList);
      if (this.powerList.length === 0) {
        return this.$zero.ToastError("请选择要调整到的部门");
      }

      let useridArr = [];
      for (let i = 0; i < this.multipleSelection.length; i++) {
        useridArr.push(this.multipleSelection[i].id);
      }

      this.changeUserInDep({
        userid: useridArr.join(","),
        type: 2,
        to: this.powerList[0].depid,
        from: this.clickShowDep.depid
      });
    },

    // 修改用户所在部门
    async changeUserInDep(obj) {
      let company_id = this.$zero.getCompany_id();
      if (!company_id) {
        return this.$zero.ToastError("企业id不存在，请重新登录");
      }
      // 如果是子部门调整到子部门 加上from
      // 如果是从企业根目录 调整部门成员 不加from
      let postObj = {
        company_id: company_id,
        userid: obj.userid,
        type: obj.type,
        depid: obj.depid,
        to: obj.to
      };

      if (!this.clickShowDep.isCorp) {
        postObj.from = obj.from;
      }

      let changeResult = await this.$api.addressBook.changeUserInDep(postObj);

      if (changeResult.statusCode === 72305) {
        return this.$zero.ToastError("分组不存在或已在该分组中");
      } else if (changeResult.statusCode === 72315) {
        return this.$zero.ToastError("请核对公司id");
      } else if (changeResult.statusCode === 75500) {
        return this.$zero.ToastError("服务异常，请稍后重试");
      }

      if (changeResult.statusCode !== 75200) {
        return this.$zero.ToastError(
          `更换分组失败，错误码:${changeResult.statusCode}`
        );
      }

      // 成功
      this.$zero.ToastSuccess("更换分组成功");
      this.dialogShow = false;
      this.powerList = [];

      // console.log("这个是什么");
      // console.log("当前点击的是什么");
      // console.log(this.clickShowDep);

      if (this.clickShowDep.isCorp) {
        // 如果是企业列表调整的话 右侧的人员列表就不需要动了
        // 但是需要清除已选择的
        this.clearAllSelection();
        return;
      }

      // 清除当前移动到其他部门的人员
      // var deleteCount = 0;
      for (let i = this.tableData.length - 1; i >= 0; i--) {
        for (let j = 0; j < this.multipleSelection.length; j++) {
          if (this.tableData[i].id === this.multipleSelection[j].id) {
            this.tableData.splice(i, 1);
            break;
          }
        }
      }
    },

    // 清除右侧人员左侧的所有的选择状态
    clearAllSelection(rows) {
      if (rows) {
        rows.forEach(row => {
          this.$refs.multipleTable.toggleRowSelection(row);
        });
      } else {
        this.$refs.multipleTable.clearSelection();
      }
    },

    resetBtn() {
      console.log("点击了重置");
    },
    // 修改成员所在部门 end

    // 添加/修改部门名称 子部门的点击按钮
    async addChildrenDepBtn() {
      console.log("点击了添加子部门");
      console.log(this.addChildrendepInput);
      // 去除空格
      let childrenValue = this.addChildrendepInput.trim();
      // if(this.addChildrenDepBtn)
      if (!childrenValue) {
        return this.$zero.ToastError("请输入部门名称");
      }
      // 判断是新建还是修改
      if (this.isAddOrChange === "add") {
        // 新建子部门 调用新增部门
        this.postAddDep({
          name: childrenValue,
          parentId: this.addChildrendepParentId
        });
      } else if (this.isAddOrChange === "change") {
        // 修改部门名称
        console.log("修改部门名称");
        this.updateDepName({
          name: this.addChildrendepInput,
          parentId: this.addChildrendepParentId,
          depid: this.changeDepNameId
        });
      } else {
        return this.$zero.ToastError("错误,无法判断新增部门或修改部门名称");
      }
    },

    /**
     * 修改部门名称
     * @params {Object} obj
     *  obj.name {String} 修改过后的部门名称
     *  obj.parentId {String} 当前修改名称的父级部门id
     *  obj.depid {String} 修改的部门的id
     */
    async updateDepName(obj) {
      let company_id = this.$zero.getCompany_id();
      if (!company_id) {
        return this.$zero.ToastError("无法获取到企业id");
      }
      let updateResult = await this.$api.addressBook.updateDepName({
        company_id: company_id,
        depid: obj.depid,
        name: obj.name,
        parentId: obj.parentId
      });

      console.log("更新名称结果");
      console.log(updateResult);
      if (updateResult.statusCode === 72305) {
        return this.$zero.ToastError("修改失败：部门名称已存在");
      } else if (updateResult.statusCode === 72315) {
        return this.$zero.ToastError("修改失败：请核对公司id");
      } else if (updateResult.statusCode === 75500) {
        return this.$zero.ToastError("服务异常，请稍后重试");
      }

      if (updateResult.statusCode !== 75200) {
        return this.$zero.ToastError(
          `修改失败，错误码${updateResult.statusCode}`
        );
      }

      // 此处以后为修改成功
      // console.log('修改成功');
      this.$zero.ToastSuccess("部门名称修改成功");
      this.clearCoverData();
      // 修改成功之后通知子组件 将集团名称改变
      this.$zero.eventBus.$emit("ad10changeDep", {
        depid: obj.depid,
        name: obj.name
      });
    },

    // 批量删除确定按钮
    async submitDelMember() {
      console.log("点击了确定");
      let delUser = [];
      for (var i = 0; i < this.multipleSelection.length; i++) {
        delUser.push(this.multipleSelection[i].id);
      }
      // console.log('选择的用户数组');
      // console.log(delUser);
      let delUserStr = delUser.join(",");
      // console.log(delUserStr);

      let result = await this.$api.addressBook.deleteUserByUserid({
        company_id: "5d1463a61609d536e0bf2a1e",
        userid: delUserStr
      });
      console.log("删除结果");
      console.log(result);
      this.clearCoverData();

      if (result.statusCode === 72305) {
        return this.$zero.ToastError("删除失败：用户不存在");
      } else if (result.statusCode === 72315) {
        return this.$zero.ToastError("删除失败：请核对公司id");
      } else if (result.statusCode === 75500) {
        return this.$zero.ToastError("删除失败：服务器繁忙，请稍后再试");
      }

      if (result.statusCode !== 75200) {
        return this.$zero.ToastError(`删除用户失败，错误码：${result.statusCode}`);
      }

      // 删除成功
      this.$zero.ToastSuccess("删除成功");
      // 执行算法 将tableData中的数据删除掉
      // var deleteCount = 0;
      for (let i = this.tableData.length - 1; i >= 0; i--) {
        for (let j = 0; j < this.multipleSelection.length; j++) {
          if (this.tableData[i].id === this.multipleSelection[j].id) {
            this.tableData.splice(i, 1);
            break;
          }
        }
      }
      this.multipleSelection = [];
    },

    // 点击批量删除按钮
    async deleteUesr() {
      let deleteNum = this.multipleSelection.length;
      if (deleteNum === 0) {
        return this.$zero.ToastError("请选择要删除的成员");
      }
      // this.isShowCover = true;
      // this.isShowDelMember = true;

      let isSubmit = await this.$zero.DeleteTips("确定将用户从企业中删除吗?");

      if (!isSubmit) {
        return;
      }
      // 此处就是去确定了
      this.submitDelMember();
    },

    // 去请求公司所有的人员
    async getComponetTopDepid() {
      let result = await this.$api.addressBook.getDepAllMember({});
      // console.log(result);
      var all = [];
      for (let i = 0; i < result.list.length; i++) {
        var userInfoItem = result.list[i];
        // console.log(userInfoItem);
        var obj = {
          id: userInfoItem.userid,
          name: userInfoItem.name,
          position: userInfoItem.openid.position,
          phone: userInfoItem.openid.phone,
          email: userInfoItem.openid.email,
          isEnable: userInfoItem.enable,
          isActive: userInfoItem.activation
        };
        all.push(obj);
      }

      // this.tableData = this.tableData.concat(all);
      this.tableData = all;
      this.initScroll();
    },

    // 处理是否启用
    handleEnable(item) {
      console.log("启用状态");
      console.log(item);
      var changeType = item.isEnable;
      // 改变状态  状态修改 0 --> 1    1--->0
      // 0 禁用  1启用 --> 传参数  1 2
      changeType = changeType === 0 ? 2 : 1;

      var obj = {
        userid: item.id,
        type: changeType
      };
      this.toggleEnableStatus(obj, item);
    },

    // 邀请
    invitationActive(item) {
      console.log("点击了邀请");
      console.log(item);
    },

    // 修改导航的颜色
    setTableNavColor() {
      return "background:#f2f2f2";
    },

    // 添加部门点击 X 遮罩层
    clodeAddCover() {
      this.clearCoverData();
    },
    // 阻止事件冒泡
    clodeAddCoverStop(e) {
      e.stopPropagation();
      e.cancelBubble = true;
    },

    // 点击遮罩层空白区域关闭
    clickCoverBlank() {
      this.clearCoverData();
    },

    // 选择部门获得焦点
    selectDepartInputFocus() {
      console.log("选择部门获得了焦点");
      this.isFocusSelectDepartInput = true;
    },

    // 添加部门
    addDepartment() {
      console.log("添加部门");
      this.isShowCover = true;
      this.isShowAddDepartments = true;
    },

    // 添加部门
    addDepBtn() {
      console.log(this.departInput);
      if (!this.departInput.trim()) {
        return this.$zero.ToastError("无效的部门名称，请输入部门名称");
      }
      if (!this.selectDepartInput.depid) {
        return this.$zero.ToastError("请选择上级部门");
      }
      // 调用新增部门API
      this.postAddDep({
        name: this.departInput.trim(),
        parentId: this.selectDepartInput.depid
      });
    },

    /**
     * 新增部门
     * @params obj {Object}
     *  obj.parentId {String} 部门父级id
     *  obj.name {String} 新增部门名称
     */
    async postAddDep(obj) {
      // obj.company_id {String} 部门公司id
      let company_id = this.$zero.getCompany_id();
      if (!company_id) {
        return this.$zero.ToastError("企业id不存在请重新登录");
      }
      let result = await this.$api.addressBook.addDepartment({
        name: obj.name,
        parentId: obj.parentId,
        company_id: company_id
      });

      console.log("新增部门结果");
      console.log(result);

      if (result.statusCode === 72305) {
        return this.$zero.ToastError("创建失败：该部门已存在");
      } else if (result.statusCode === 72315) {
        return this.$zero.ToastError("创建失败：请核对公司id");
      } else if (result.statusCode === 75500) {
        return this.$zero.ToastError("创建失败：服务异常，请稍后重试");
      }

      if (result.statusCode !== 75200) {
        return this.$zero.ToastError(`创建失败：错误码${result.statusCode}`);
      }

      // 创建成功
      this.$zero.ToastSuccess("部门创建成功");
      // 清除遮罩层 清空里面的值
      this.clearCoverData();
      let newDepInfo = {
        depid: result.depid,
        name: obj.name,
        parentId: obj.parentId,
        type: "dep",
        show: false // 是否展开
      };
      console.log("新增出来的当前的部门");
      console.log(newDepInfo);

      // 新增部门了
      this.$zero.eventBus.$emit("ad10Add", {
        depinfo: newDepInfo
      });
    },

    /**
     * 切换用户的启用与禁用状态
     * @params obj {Object}
     *  obj.company_id {String} 公司id
     *  obj.userid {String} 用户id
     *  obj.type {Number} 修改类型
     * @params item {Object} 一行用户信息数据项
     */
    async toggleEnableStatus(obj, item) {
      // console.log(arguments);

      let company_id = this.$zero.getCompany_id();
      if (!company_id) {
        return this.$zero.ToastError('企业id不存在，请重新登录');
      }

      let result = await this.$api.addressBook.toggleEnableStatus({
        company_id: company_id,
        userid: obj.userid,
        type: obj.type
      });

      console.log("修改结果");
      console.log(result);

      if (result.statusCode === 75500) {
        return this.$zero.ToastError("状态修改失败：服务异常，请稍后重试");
      }

      if (result.statusCode !== 75200) {
        return this.$zero.ToastError(`修改状态失败：错误码：${result.statusCode}`);
      }

      // 成功
      this.$zero.ToastSuccess("状态修改成功");
      if (obj.type === 1) {
        item.isEnable = 0;
      } else if (obj.type === 2) {
        item.isEnable = 1;
      }
    },

    /**
     * 删除用户
     * @params obj {Object}
     */
    async deleteUserByUserid(obj) {
      let result = await this.$api.addressBook.deleteUserByUserid({
        company_id: obj.company_id,
        userid: obj.userid
      });
      console.log("删除结果");
      console.log(result);

      if (result.statusCode === 72305) {
        return this.$zero.ToastError("删除失败：用户不存在");
      } else if (result.statusCode === 72315) {
        return this.$zero.ToastError("删除失败：请核对公司id");
      } else if (result.statusCode === 75500) {
        return this.$zero.ToastError("删除失败：服务器繁忙，请稍后再试");
      }

      if (result.statusCode !== 75200) {
        return this.$zero.ToastError(`删除失败：错误码${result.statusCode}`);
      }

      // 删除成功
      this.$zero.ToastSuccess("用户删除成功");
    },

    // 邀请新成员
    addNewMember() {
      console.log("邀请新成员");
      this.isShowCover = true;
      this.isShowAddNewMember = true;
    },

    // 清空所有弹出层的数据
    clearCoverData() {
      this.isShowCover = false;
      this.isShowAddDepartments = false;
      this.isShowAddNewMember = false;
      this.isFocusSelectDepartInput = false;
      this.isShowDelMember = false;
      this.isShowAddDep = false;
      this.departInput = "";
      this.selectDepartInput = {
        val: "",
        depid: ""
      };
      this.isAddOrChange = "";
      this.addChildrendepParentId = "";
      this.changeDepNameId = "";
      this.addChildrendepInput = "";
    },

    // 选择时的点击事件
    handleSelectionChange(val) {
      console.log("点击的时候");
      console.log(val);
      this.multipleSelection = val;
    },

    /**
     * 根据用户id获取用户的详细信息
     * @params {Object} obj
     *  obj.userid {String} 用户id
     */
    async getUserDetail(obj) {
      let company_id = this.$zero.getCompany_id();
      if (!company_id) {
        return this.$zero.ToastError("无效的company_id,请求用户信息失败");
      }

      let userDetail = await this.$api.addressBook.getUserDetail({
        company_id: company_id,
        userid: obj.userid
      });

      if (!userDetail.statusCode === 72305) {
        return this.$zero.ToastError("该成员不存在,请核对公司是否对应");
      } else if (userDetail.statusCode === 72315) {
        return this.$zero.ToastError("请核对公司id");
      } else if (userDetail.statusCode === 75500) {
        return this.$zero.ToastError("服务异常，请稍后重试");
      }

      if (userDetail.statusCode !== 75200) {
        return this.$zero.ToastError(
          `查询用户信息失败，错误码:${userDetail.statusCode}`
        );
      }

      // 查询用户信息成功
      console.log("查询用户信息成功");
      console.log(userDetail);
      console.log(userDetail.info);
      var userItem = {
        id: userDetail.info.userid,
        name: userDetail.info.name,
        position: userDetail.info.position,
        phone: userDetail.info.phone,
        email: userDetail.info.email,
        isEnable: userDetail.info.enable,
        isActive: userDetail.info.activation
      };
      this.tableData = [userItem];
    },

    // 根据部门 id 请求部门人员
    async getMemberByDepid(depid) {
      let result = await this.$api.addressBook.getDepAllMember({
        depid: depid
      });
      console.log("请求部门人员");
      console.log(result);
      var all = [];
      for (let i = 0; i < result.list.length; i++) {
        var userInfoItem = result.list[i];
        // console.log(userInfoItem);
        var obj = {
          id: userInfoItem.userid,
          name: userInfoItem.name,
          position: userInfoItem.openid.position,
          phone: userInfoItem.openid.phone,
          email: userInfoItem.openid.email,
          isEnable: userInfoItem.enable,
          isActive: userInfoItem.activation
        };
        all.push(obj);
      }

      // this.tableData = this.tableData.concat(all);
      this.tableData = all;
      this.initScroll();
    },

    // 点击选择上级部门的文字
    clickParentDepTxt(item) {
      this.selectDepartInput.val = item.name;
      this.selectDepartInput.depid = item.depid;
      // 关闭打开的
      this.isFocusSelectDepartInput = false;
    },

    // 删除部门
    async deleteDepFn({ depid, name }) {
      let company_id = this.$zero.getCompany_id();
      if (!company_id) {
        return this.$zero.ToastError("企业id不存在，请重新登录");
      }

      // 调起弹框
      let isSubmit = await this.$zero.DeleteTips(`确定删除${name}吗`);
      if (!isSubmit) {
        return;
      }

      // 调用删除部门接口
      let deleteResult = await this.$api.addressBook.deleteDep({
        company_id: company_id,
        depid: depid
      });

      if (deleteResult.statusCode === 72309) {
        return this.$zero.ToastError(
          "不能删除包含成员的部门"
        );
      } else if (deleteResult.statusCode === 72315) {
        return this.$zero.ToastError("请核对公司id");
      } else if (deleteResult.statusCode === 75500) {
        return this.$zero.ToastError("服务异常，请稍后重试");
      }

      if (deleteResult.statusCode !== 75200) {
        return this.$zero.ToastError(
          `删除失败，错误码:${deleteResult.statusCode}`
        );
      }

      // 以下就是删除成功
      this.$zero.ToastSuccess("删除成功");
      // 通知子组件删除成
      this.$zero.eventBus.$emit("ad10Del", { depid }); // 删除成功的部门id 通知子组件将集团树中的此部门删除，动态响应视图
    },

    // 点击了部门的 监听函数
    clickDepEmit(obj) {
      let { item, corpTreeId } = obj;
      console.log("此处是点击集团树id ==== 的" + corpTreeId + "执行，点击的文字执行的函数在此处");

      // corpTreeId 本页面中  ad10 是点击左侧的文字 ad11点击的是新建部门的展开的 ad12为调整用户部门
      if (corpTreeId === "ad10") {
        // 此处是组织架构左侧的公司部门的点击
        // console.log(corpTreeId);
        // console.log('点击');
        console.log("当前点击的元素");
        console.log(item);

        this.clickShowDep = item; // 修改当前点击的元素
        // 这里检查 是否是公司
        if (item.isCorp) {
          // 请求公司
          this.getComponetTopDepid();
        } else if (item.type === "dep") {
          // 请求部门
          this.getMemberByDepid(item.depid);
        } else if (item.type === "user") {
          // 这里的这个函数应该永远不会走到了 因为左侧没有人员了
          // 请求人员的信息
          this.getUserDetail({ userid: item.depid });
        }
      } else if (corpTreeId === "ad11") {
        // 此处是点击的 选择上机部门触发的函数
        console.log("此处是点击的 选择上机部门触发的函数");
        // console.log(corpTreeId);
        // console.log('点击');
        this.clickParentDepTxt(item);
      } else if (corpTreeId === "ad12") {
        // 此处是点击的调整用户部门触发的函数
        console.log("此处是点击的调整用户部门触发的函数");
        // console.log(corpTreeId);
        // console.log('点击');
      }
    },

    // 添加子部门
    addChildDep(obj) {
      // console.log('添加子部门父组件');
      // console.log(obj);
      let { corpTreeId, item } = obj;
      if (corpTreeId !== "ad10") {
        return;
      }

      // 展开弹出层
      this.isShowCover = true;
      this.isShowAddDep = true;
      this.isAddOrChange = "add"; // 设置为新增部门
      this.addChildrendepParentId = item.depid; // 当前id为父级id
    },

    // 修改部门名称
    changeDepName(obj) {
      console.log("修改部门名称");
      console.log(obj);

      let { corpTreeId, item } = obj;

      if (corpTreeId !== "ad10") {
        return;
      }
      // 展开弹出层
      this.isShowCover = true;
      this.isShowAddDep = true;
      this.isAddOrChange = "change"; // 设置为部门
      this.addChildrendepParentId = item.parent_id; // 设置父级id
      this.addChildrendepInput = item.name;
      this.changeDepNameId = item.depid; // 当前修改的部门id
    },

    // 删除部门
    deleteDep(obj) {
      console.log("删除部门");
      console.log(obj);
      let { corpTreeId, item } = obj;
      if (corpTreeId !== "ad10") {
        return;
      }

      console.log("调用删除部门接口");
      // 调用删除部门接口
      this.deleteDepFn({
        depid: item.depid,
        name: item.name
      });
    },

    // 初始化滚轮
    initScroll() {
      // 滚轮
      this.$nextTick(() => {
        $(".el-table__body-wrapper")
          .getNiceScroll()
          .remove();
        $(".el-table__body-wrapper").niceScroll(".el-table__body", {
          cursorcolor: "#2f78ef",
          cursorwidth: "5px",
          autohidemode: false,
          background: "#e6e6e6",
          preventmultitouchscrolling: true,
          bouncescroll: false
        });
      });
    },

    tellCorpDep(corpInfo) {
      // console.log("企业信息");
      // console.log(corpInfo);
      this.clickShowDep = corpInfo;
    }
  },
  //添加删除公共事件
  beforeDestroy() {
    this.$zero.eventBus.$off("addBookLoadMore");
  },
  mounted() {
    // 获取公司所有的人
    this.getComponetTopDepid();

    // 点击了选择部门 --- 监听
    // 点击公司组织架构的文字触发的函数
    this.$zero.eventBus.$on("departmentItem", this.clickDepEmit);

    // 监听添加子部门
    this.$zero.eventBus.$on("addChildDep", this.addChildDep);

    // 监听修改部门名称
    this.$zero.eventBus.$on("changeDepName", this.changeDepName);

    // 监听删除部门
    this.$zero.eventBus.$on("deleteDep", this.deleteDep);

    // 监听请求到企业
    this.$zero.eventBus.$on("tellCorpDep", this.tellCorpDep);

    // 初始化滚轮
    this.initScroll();
  },

  destroyed() {
    this.$zero.eventBus.$off("departmentItem", this.clickDepEmit);
    // 卸载添加子部门
    this.$zero.eventBus.$off("addChildDep", this.addChildDep);
    // 卸载修改子部门名称
    this.$zero.eventBus.$off("changeDepName", this.changeDepName);
    // 卸载删除部门
    this.$zero.eventBus.$off("deleteDep", this.deleteDep);
    // 卸载监听请求到企业
    this.$zero.eventBus.$off("tellCorpDep", this.tellCorpDep);
  },
  components: {
    DepTree,
    MaskLayer
  }
};
</script>

